/* Navbar */

ul {
    list-style-type: none;
    margin: 0;
    padding: 35px;
    margin-left: 100px;
  }
  
  li {
    float: left;
  }

  li a {
    padding: 8px 15px;
    font-size: large;
    color: #000;
    text-decoration: none;
  }
  .main{
    display: flex;
    margin-left: 150px;
  }
  img{
    margin-top: 15px;
  }

  /* Carrers */

  .sub-heading{
    color: #0f62fe;
  }
  .content-des {
    padding: 10px 100px 20px 100px;
    font-size: 22px;
    color: #777777;
  }
  #border1 p{
     color: #000;
     font-size: 20px;
  }
  #border1{
    border-bottom:8px solid #FE0000 !important;
    border-radius: 10px !important;
  }
  .border span{
    font-size: 16px;
  }
  .border{
    gap: 10px;
  }
  footer {
    /* position: absolute;
    bottom: -55px; */
    width: 100%;
    background-color: #333;
    color:#fff;
  }
  